import React, { useEffect, useState, useRef } from 'react';
import styles from './index.less';

type IDot = [number, number];
type IPolygon = [IDot, IDot, IDot];

// 在一个空白页面放入设计图，通过点击每个点，自动计算到页面左边、顶端的百分比。
const Draw = () => {
  const [dotList, setDotList] = useState<IDot[]>([]);
  const [polygon, setPolygon] = useState<IPolygon>([[50, 50], [50, 50], [50, 50]]);
  const previewRef = useRef<HTMLDivElement>(null);
  const handleClick = (e: any) => {
    const mouseX = e.pageX;
    const mouseY = e.pageY;
    const shapesoffsetX = previewRef.current?.offsetLeft || 0;
    const shapesoffsetY = previewRef.current?.offsetTop || 0;
    const polygonsWidth = previewRef.current?.clientWidth || 0;
    const polygonsHeight = previewRef.current?.clientHeight || 0;
    const shapesmouseX = mouseX - shapesoffsetX;
    const shapesmouseY = mouseY - shapesoffsetY;
    const mousepercentX = shapesmouseX / polygonsWidth;
    const mousepercentY = shapesmouseY / polygonsHeight;
    const finalmouseX = (mousepercentX) * 100;
    const finalmouseY = (mousepercentY) * 100;
    const normalisedX = +finalmouseX.toFixed(3);
    const normalisedY = +finalmouseY.toFixed(3);
    dotList.push([normalisedX, normalisedY]);
    setDotList([...dotList]);
  };

  useEffect(() => {
    if (dotList.length >= 3) {
      setPolygon([...dotList as IPolygon]);
      setDotList([]);
    }
  }, [dotList]);
  return (
    <div className={styles.container}>
      <div className={styles.content} onClick={handleClick} ref={previewRef}>
        <div className={styles.wallpaper} />
        <div className={styles.pieces} style={{ WebkitClipPath: `polygon(${polygon.map(i => `${i[0]}% ${i[1]}%`).join(', ')})` }} />
        {dotList.map((item, index) => (
          <div className={styles.dot} key={index} style={{ left: `${item[0]}%`, top: `${item[1]}%` }} />
        ))}
      </div>
    </div>
  );
};

export default Draw;
