import React from 'react';
import { Avatar } from 'antd';
import Layout from '@/components/Layout';
import LogoView from '@/components/LogoView';
import Copyright from '@/components/Copyright';
import Menu from '@/module/view/Menu';
import Main from '@/module/view/Main';
import Showroom from '@/module/view/Showroom';
import styles from './app.less';

function App({ view }: any) {
  console.log('view', view);
  if (view === 'showroom') {
    return <Showroom />;
  } else {
    return (
      <main className={styles.main}>
        <Layout
          icon={(
            <LogoView
              icon={(
                <Avatar
                  shape="square"
                  size={80}
                  style={{ marginBottom: '8px' }}
                  className={styles.avatarColor}
                >
                  SPECIES
                </Avatar>
              )}
              title="30 PIECES"
              subTitle="30种濒临灭绝动物CSS拼图"
            />
          )}
          menu={<Menu />}
          main={<Main />}
          copyright={<Copyright />}
        />
      </main>
    );
  }
}

export default App;
