import React, { useEffect, useState } from 'react';
import classnames from 'classnames';
import { usePageViewStore } from '@/stores';
import Pieces from '@/module/view/Pieces';
import Draw from '@/module/view/Draw';
import Intro from '@/module/view/Intro';
import styles from './index.less';

const Main = () => {
  const pageView = usePageViewStore((state) => state.pageView);
  console.log('pageView', pageView);
  const [page, type] = pageView.split('.');
  return (
    <div className={styles.container}>
      <div className={styles.content}>
        {page === 'pieces' && (<Pieces type={type} />)}
        {page === 'intro' && (<Intro type={type} />)}
        {page === 'debug' && (<Draw />)}
      </div>
    </div>
  );
};

export default Main;
