import React, { useEffect, useState } from 'react';
import Pieces from '@/module/view/Pieces';
import styles from './index.less';

const Showroom = () => {
  const array = ['crow', 'vaquita', 'tamarin', 'frog'];
  const [currentIndex, setCurrentIndex] = useState(0);
  useEffect(() => {
    const tick = () => setCurrentIndex((currentIndex + 1) % array.length);
    const timeId = setTimeout(tick, 300000);
    return () => clearTimeout(timeId);
  }, [currentIndex]);
  return (
    <div className={styles.container}>
      <Pieces type={array[currentIndex]} className={styles.pieces} />
    </div>
  );
};

export default Showroom;
