import { StateCreator } from 'zustand';
import { defaultSelectedKeys } from '@/module/view/Menu/data';

export interface PageViewSlice {
  pageView: string;
  setPageView: (pageView: string) => void;
};

export const createPageViewSlice: StateCreator<PageViewSlice> = (set) => ({
  pageView: defaultSelectedKeys,
  setPageView: (pageView) => (set({ pageView })),
});
