import React from 'react';
import classnames from 'classnames';
import styles from './index.less';

const Pieces = ({ type, className }: { type: string, [key: string]: any }) => {
  console.log('Pieces Type:', type);
  return (
    <div className={classnames(styles.container, styles[type] || styles.main, className)}>
      <div className={styles.panel}>
        {/* 30个基础碎片 */}
        {(new Array(30)).fill('').map((i, index) => (
          <div className={styles.wrapper} key={`shard-${index}`}>
            <div className={styles.pieces} />
          </div>
        ))}
        {/* 3个外围设备碎片 */}
        {(new Array(3)).fill('').map((i, index) => (
          <div className={classnames(styles.wrapper, styles.extra)} key={`shard-${index + 30}`}>
            <div className={styles.pieces} />
          </div>
        ))}
        {/* 影子 */}
        <div className={styles.shadow} />
      </div>
    </div>
  );
};

export default Pieces;
